.curCityTxt {
  font-size: 16px;
  line-height: 32px;
  color: #666;
  margin: 12px;
  text-align: center;
}
.cityList {
  margin: 20px 12px 0 12px;
}
.cityList li {
  width: 25%;
  display: inline-block;
  margin-bottom: 10px;
  text-align: center;
}
.cityList li span {
  display: inline-block;
  height: 32px;
  line-height: 32px;
  font-size: 14px;
  color: #999;
  text-align: center;
  width: 80%;
  border: 1px solid #999;
  border-radius: 6px;
}



