.swipeWrap {
  position: relative;
}
.carousel-item {
  height: 140px;
}
.carousel-item > img {
  width: 100%;
  height: 100%;
}
.index-container {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.index-container ul {
  text-align: center;
}
.index-container ul li {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #25b7aa;
  margin: 0 5px;
  display: inline-block;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}
.index-container ul li.selected {
  width: 18px;
  border-radius: 6px;
}
.sliderBone {
  height: 140px;
  background: #d3d3d3;
  font-size: 26px;
  color: #25b7aa;
  text-align: center;
  line-height: 120px;
}



