.list li {
  height: 104px;
  background: #FFF;
  position: relative;
  list-style: none;
}
h3 {
  color: #25b7aa;
  font-size: 16px;
  line-height: 32px;
  padding-left: 8px;
  font-weight: normal;
  margin-top: 4px!important;
  margin-left: 12px!important;
}
.list li > a > img {
  float: left;
  height: 84px;
  width: 84px;
  margin-left: 12px;
  margin-top: 10px;
}
.list li:after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 12px;
  right: 10px;
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.3);
      -ms-transform: scaleY(0.3);
          transform: scaleY(0.3);
}
.list li .content {
  padding-left: 100px;
  padding-top: 6px;
  position: relative;
  height: 100%;
}
.itemTit {
  font-size: 14px;
  color: #333;
  line-height: 28px;
}
.itemInfo {
  font-size: 12px;
  color: #666;
  line-height: 22px;
}
.addToCartBtn {
  display: inline-block;
  width: 32px;
  height: 32px;
  position: absolute;
  right: 12px;
  bottom: 12px;
  background: #25bbaa;
  border-radius: 5px;
  line-height: 32px;
}
.addToCartBtn i {
  color: #FFF;
  font-size: 20px;
  line-height: 32px;
  margin-left: 5px;
}



