.imgBox {
  height: 120px;
}
.imgBox > img {
  height: 120px;
  width: 100%;
}
.contentBox {
  height: 96px;
  background: #FFF;
  padding: 10px 12px ;
}
.contentBox h4 {
  font-size: 16px;
  color: #333;
  line-height: 28px;
}
.contentBox p {
  color: #666;
  font-size: 14px;
  line-height: 22px;
}
.contentBox p > span {
  margin-right: 22px;
}
.disc {
  background: #FFF;
  margin-top: 12px;
  padding: 12px;
  min-height: 300px;
  color: #666;
  font-size: 14px;
}
.loadTip {
  font-size: 18px;
  color: #25B7AA;
  margin-top: 20px;
  text-align: center;
}
.bottomToolBox {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background: #FFF;
}
.addCartBtn {
  display: inline-block;
  height: 100%;
  padding: 0 18px;
  background: #25b7aa;
  color: #FFF;
  float: right;
  line-height: 50px;
  text-align: center;
}



