.cartList li {
  height: 88px;
  padding: 12px;
  background: #FFF;
  position: relative;
  list-style: none;
}
.cartList li:not(:last-child)::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 12px;
  right: 10px;
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.3);
  -ms-transform: scaleY(0.3);
  transform: scaleY(0.3);
}
.cartList li > img {
  height: 64px;
  width: 64px;
  float: left;
}
.itemContent {
  padding-left: 70px;
  font-size: 14px;
  position: relative;
}
.itemContent h4 {
  font-weight: normal;
  color: #333;
}
.itemContent p {
  color: #666;
}
.countBox {
  position: absolute;
  right: 6px;
  bottom: 2px;
  width: 96px;
  height: 30px;
  border: 1px solid #22bbaa;
}
.reduceBtn,
.addBtn {
  display: inline-block;
  width: 32%;
  background: #22bbaa;
  color: #FFF;
  height: 100%;
  text-align: center;
  line-height: 30px;
}
.countBox > i {
  width: 36%;
  display: inline-block;
  font-style: normal;
  text-align: center;
  line-height: 30px;
}
.accountTxt {
  line-height: 30px;
  font-size: 15px;
  margin-top: 2px;
  text-align: right;
  padding-right: 18px;
  color: #666;
}
.cartIsEmpty {
  padding: 160px 20px 20px 20px;
  text-align: center;
  color: #666;
  font-size: 16px;
}
.cartIsEmpty > div {
  margin-top: 12px;
}
.linkBtn {
  display: inline-block;
  width: 100px;
  height: 36px;
  background: #22bbaa;
  line-height: 36px;
  color: #FFF;
  text-align: center;
  border-radius: 6px;
}



