.userInfo {
  height: 166px;
  background: #22b7aa;
  padding-top: 30px;
}
.userImg {
  display: block!important;
  margin: 0px auto 4px auto!important;
}
.name {
  font-size: 18px;
  color: #FFF;
  line-height: 32px;
  text-align: center;
}
.phone {
  font-size: 14px;
  color: #f3f3f3;
  line-height: 20px;
  text-align: center;
}
.userList {
  background: #FFF;
  margin-top: 12px;
}
.userItem {
  height: 42px;
  line-height: 42px;
  padding: 0 12px;
  font-size: 14px;
  position: relative;
}
.userItem > span {
  float: right;
}
.userItem:not(:last-child)::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 12px;
  right: 10px;
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.3);
  -ms-transform: scaleY(0.3);
  transform: scaleY(0.3);
}



