.headerWarp {
  position: relative;
}
.headerWarp h2 {
  font-size: 18px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #FFF;
  background: #25b7aa;
}
.changeCityBtn {
  font-size: 12px;
  color: #f3f3f3;
  position: absolute;
  top: 13px;
  right: 11px;
}
.changeCityBtn > a {
  color: #f3f3f3;
}
.backBtn {
  font-size: 18px;
  color: #FFF;
  position: absolute;
  top: 6px;
  left: 1px;
  padding: 4px 12px;
}



