.Tabwrap {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background: #FFF;
}
.TabItem {
  width: 33.333%;
  display: inline-block;
  text-align: center;
  position: relative;
}
.TabItem i {
  display: block;
  font-size: 20px;
  color: #666;
  margin-top: 8px;
}
.TabItem span {
  font-size: 10px;
  display: block;
  line-height: 20px;
  color: #666;
}
.TabItem strong {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #e0000a;
  top: 1px;
  right: 33%;
}
.TabItem .active i,
.TabItem .active span {
  color: #22b7aa;
}



